.main-inner {
  width: 100%;
  /* max-width: 68em; */
  margin: 2em auto;
  padding: 0 2.5em;
  font-family: Helvetica;
}

.main-title {
  font-size: 48px;
  font-family: sans-serif;
  font-weight: bold;
}

.red {
  color: #B02770;
  font-weight: bold;
}
.to-reviewer {
  border: 2px solid #B02770;
  padding: 5px;
}

.introduction {
  margin-top: 1em;
}
.speech-label {
  color: rgb(88, 195, 194);
  font-weight: 900;
  padding: 0 30px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
