.wrapper {
  border: 1px solid gray;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  margin: 1em 0;
}
.block {
  border: 1px solid #c9c9c9;
  background-color: rgba(144, 144, 144, 0.075);
}
.block-label {
  background-color: #B02770;
  color: #fff;
  font-size: 1.2em;
  font-weight: 700;
  padding: 10px 30px;
}
.voice-block {
  display: flex;
}
.voice-block-img img {
  width: 400px;
}
.voice-block-wav {
  padding-top: 100px;
}

.rowv {
  display: flex;
  padding: 1em;
}

.rowv .element {
  margin-right: 3em;
}